<script setup>
import { ref, onMounted, computed, nextTick, reactive } from "vue";
import axios from "axios";
import { log, logError } from "@/utils/logger";
import VButton from "@/components/Button.vue";
import { API_BASE_URL } from "@/config/config";

// global variables
import { useUserStore } from "@/stores/user";
const userData = useUserStore();
import { useThemeStore } from '@/stores/theme';
const theme = useThemeStore();

// api
const urlProfile = API_BASE_URL + "/perizinan";

const loadingString = "Loading...";

const user = reactive({
  keterangan: "",
  izin: "",         // <--- Tambahan untuk dropdown
  izinLabel: "",    // <--- Tambahan untuk dropdown
});

const formData = new FormData();
const data = ref();
const selectedFile = ref(null);

const handleFileUpload = (event) => {
  selectedFile.value = event.target.files[0];
};

const passwordMismatch = computed(() => user.password !== user.confirmPassword);

// Fetch data ketika komponen mounted
onMounted(async () => {
  console.log("Photo : ", userData.photo);
  try {
    const response = await axios.get(urlProfile, {
      params: {
        username: userData.username,
      },
    });
    data.value = response.data[0];
  } catch (error) {
    console.error("Fetch error:", error);
  }
});


const resetForm = () => {
  user.keterangan = "";
  user.izin = "";
  user.izinLabel = "";
};

const submitForm = async () => {
  try {
    if(user.keterangan == ""){
      alert("Keterangan Masih Kosong");
      return;
    }
    if(user.izin == ""){
      alert("Pilih Jenis Perizinan");
      return;
    }
    if (!selectedFile.value) {
      alert("Upload Foto Bukti")
      return;
    }
    formData.append("username", userData.username);
    formData.append("picture", selectedFile.value);
    formData.append("keterangan", user.keterangan);
    formData.append("izin", user.izin);

    const response = await axios.post(urlProfile, formData);
    log("RESPONSE : ", response.data);
    await nextTick();
    // window.location.reload();
    alert("Inserted successfully!");
  } catch (error) {
    logError("Submit error:", error);
    alert("Error updating profile.");
  }
};

// Dropdown logic untuk Jenis Izin
const options = [
  { value: 'datang', label: 'Datang terlambat' },
  { value: 'pulang', label: 'Pulang cepat' },
  { value: 'tidak-masuk', label: 'Tidak masuk' }
];

const dropdownOpen = ref(false);

function toggleDropdown() {
  dropdownOpen.value = !dropdownOpen.value;
}

function selectOption(option) {
  user.izin = option.value;
  user.izinLabel = option.label;
  dropdownOpen.value = false;
}
</script>


<template>
  <div class="row">
    <div class="col-xl-6 mx-auto">
      <div class="card">
        <div class="card-header px-4 py-3">
          <h5 class="mb-0">Perizinan</h5>
        </div>
        <div class="card-body p-4">
          <form
            @submit.prevent="submitForm"
            class="row g-3 needs-validation"
            novalidate
          >
            <div class="col-md-12">
              <label for="keterangan" class="form-label">Keterangan</label>
              <input
                v-model="user.keterangan"
                type="text"
                class="form-control"
                id="keterangan"
              />
            </div>

            <div class="col-md-12">
                <label for="izin" class="form-label">Jenis Izin</label>
                <div class="dropdown":class="theme.darkMode ? 'bg-dark text-white border-secondary' : 'bg-white text-dark border-secondary'">
                    <div class="dropdown-selected" @click="toggleDropdown">
                    {{ user.izinLabel || 'Pilih salah satu' }}
                    </div>
                    <transition name="slide-fade">
                      <div
                            v-if="dropdownOpen"
                            class="dropdown-options border"
                            :class="theme.darkMode ? 'bg-dark text-white border-secondary' : 'bg-white text-dark border-secondary'"
                          >
                        <div 
                        v-for="option in options" 
                        :key="option.value" 
                        class="dropdown-option" 
                        @click="selectOption(option)" 
                        >
                        {{ option.label }}
                        </div>
                    </div>
                    </transition>
                </div>
            </div>


            <!-- Upload Foto -->
            <div class="col-md-12">
              <label for="photo" class="form-label">Upload Foto Bukti</label>
              <input type="file" class="form-control" id="photo" @change="handleFileUpload" accept="image/*" />
              <div v-if="previewImage" class="mt-3">
                <img :src="previewImage" alt="Preview" class="img-thumbnail" style="max-width: 200px;" />
              </div>
            </div>

             <div class="col-md-12 mt-4 mb-3 d-flex gap-3">
              <VButton type="submit" color="danger" size="md" :block="false">
                Submit
              </VButton>
              <VButton type="reset" class="btn btn-secondary" @click="resetForm">
                Reset
              </VButton>
            </div>
          </form>
        </div>
      </div>
    </div>
  </div>
</template>

<style scoped>
/* style khusus dropdown */
.dropdown-selected {
  display: flex;
  align-items: center;
}
.dropdown {
  position: relative;
  cursor: pointer;
  height: 35px; 
  border-color: #e6e6e6;
}
.dropdown-options {
  position: absolute;
  width: 100%;
  background-color: var(--dropdown-bg, white);
  color: var(--dropdown-text, black);
  border: 1px solid #ccc;
  z-index: 10;
  margin-top: 5px;
}

.dropdown-option:hover {
  background: var(--dropdown-hover-bg, #dc3545);
  color: var(--dropdown-hover-text, white);
}

.dropdown-option {
  padding: 10px;
}
.dropdown-option:hover {
  background: #dc3545;
  color: white;
}

/* Tambahan animasi */
.slide-fade-enter-active {
  transition: all 0.3s ease;
}
.slide-fade-leave-active {
  transition: all 0.2s ease;
}
.slide-fade-enter-from {
  opacity: 0;
  transform: translateY(-10px);
}
.slide-fade-leave-to {
  opacity: 0;
  transform: translateY(-10px);
}
</style>